.homepage {
  color: rgba(173, 55, 228, 0.849);
  min-width: 100%;
  min-height: 100vh;
  background: url(../routes/imgs/desktop-1.jpg);
  background-size: fill;
}

.twitch {
  background-image: url("../routes/imgs/twitch.png");
}

.home-desc {
  margin: 0px auto;
}

.homepage h1 {
  font-size: 3.052rem;
  font-family: "snow-white";
  margin: 0px;
}

.homepage p {
  font-family: "crewniverse";
  font-size: 2.953rem;
}

.shop-now {
  font-family: "dk-lemon-yellow-sun";
  width: 10vw;
  height: 10vh;
  font-size: 4.052rem;
  text-decoration: none;
  color: white;
  background: rgba(173, 55, 228, 0.849);
  box-shadow: 5px 5px 5px #b72a5d;
}

.shop-now:hover {
  background: white;
  color: rgba(173, 55, 228, 0.849);
}

.home-banner {
  display: flex;
  justify-content: space-around;
}

.avatar {
  opacity: 80%;
  width: 40vw;
  height: 80vh;
  transform: translateY(-150px);
}

.icons {
  margin: 64px 30px 25px 0px;
  width: 64px;
  height: 64px;
  transition: transform ease-in 0.1s;
}

.icons:hover {
  transform: translateY(-20px);
}

@media (max-width: 1000px) {
  .home-banner {
    display: block;
  }

  .avatar {
    width: 100%;
    height: 80%;
    transform: translateY(-100px);
  }

  .home-desc {
    margin: 0px auto;
    text-align: center;
  }

  .home-desc h1,
  .home-desc p {
    font-size: 6vw;
  }
}
