.shopping-cart {
  visibility:hidden;
  display: flex;
  flex-direction: column;
  background-color: #ffdae8;
  width: 400px;
  height: 100vh;
  align-self: end;
  float: right;
  position: absolute;
  left: 100%;
  z-index: 999;
  transition:left .25s ease-in;

}

.total {
  font-size: 1.5rem;
  text-align: end;
  margin-right: 26px;
}

.shopping-cart-active {
  
  visibility: visible;
  display: flex;
  flex-direction: column;
  background-color: #ffdae8;
  width: 400px;
  height: 100vh;
  align-self: end;
  float: right;
  position: absolute;
  left: 80%;
  z-index: 999;
  transition:left .25s ease-in;

}

.cart-list {
  padding: 0px;
  height: 65%;
  overflow-y: scroll;
}

.cart-info p {
  margin-top: 0px;
}

.thumbnail {
  width: 27%;
  height: 30%;
}

.cart-info {
  margin-left: 10px;
  width: 70%;
  font-size: 1.25rem;
  line-height: 24px;
}

.cart-item {
  display: flex;
  margin: 20px;
}
.cart-info {
  font-size: 1.25rem;
  margin-left: 10px;
}

.list {
  list-style-type: none;
}

.quantity {
  height: 30%;
  width: 100%;
  display: flex;

  justify-content: space-between;
}

.min,
.plus {
  width: 50%;
  background: none;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5rem;
  cursor: pointer;
  border: none;
  color: white;
}
.buttons {
  display: flex;
  width: 30%;
  align-items: center;
  border-style: double;
  border-color: rgba(173, 55, 228, 0.849);
  background-color: rgba(173, 55, 228, 0.849);
  color: white;
}

.rm {
  width: 30%;
  border-style: double;
  background-color: rgba(173, 55, 228, 0.849);
  border-color: rgba(173, 55, 228, 0.849);
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5rem;
  cursor: pointer;
  font-family:'crewniverse';
}

.rm:hover {
  background-color: rgba(72, 2, 104, 0.85);
}

.checkout {
  font-family:'crewniverse';

  background: none;
  border-style: double;
  color: white;
  background-color: rgba(173, 55, 228, 0.849);
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5rem;
  cursor: pointer;
  width: 70%;
  margin: 0px auto;
  height: 5%;
}

.checkout:hover {
  background-color: rgba(72, 2, 104, 0.85);
}

.quantity p {
  text-align: center;
  margin: 0px;
  padding: 0px;
  font-size: 1.5rem;
}


@media (max-width: 1000px) {
.shopping-cart-active{
  width: 50%;
  position: relative;
  z-index: 999;
  left: 0%;
}
}



@media (max-width: 460px) {
  .shopping-cart {
    visibility:hidden;
    display: none;
    flex-direction: column;
    background-color: #ffdae8;
    width: 400px;
    height: 100vh;
    align-self: end;
    float: right;
    position: absolute;
    left: 100%;
    z-index: 99;
    transition:left .25s ease-in;
  
  }
  .shopping-cart-active{
    width: 100%;
    position: relative;
    z-index: 999;
    left: 0%;
    transition:left .25s ease-out;

  }
  }
