.links, .links-mobile {
  display: flex;
  justify-content: space-evenly;
}
.shopButt, .shopButt-mobile {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cart-info {
  margin-left: 10px;
}

.cart-item {
  display: flex;
  margin: 20px;
}
.cart-info {
  font-size: 1.25rem;
}
nav {
  display: flex;
  flex-basis: 0;
  align-items: center;
  justify-content: space-evenly;
  background-color: #ffdae8;
  font-family: "dk-lemon-yellow-sun";
}
nav button {
  margin: 10px;
  background-image: url(./routes/imgs/shopping-cart.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 30px;
  background-color: #f8ebe6;
  border-radius: 30%;
  width: 50px;
  height: 50px;
}

nav button span {
  display: none;
}
nav a {
  font-size: 2.441rem;
  margin: 20px;
}

nav h1 {
  font-size: 3.052rem;
}



.shop-page h1 {
  text-align: center;

  font-family: "snow-white";
}


.item-info {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}




.mobile-nav{display:none}


@media(max-width:460px){
  .links, .shopButt{display:none}
  nav h1{text-align: center;}

  .mobile-nav{
    z-index:999;
    height:6vh;
    width:100%;
    display:flex;
    justify-content: space-around;
position: fixed;
top: 94vh;
left: 0vw;

  }

  .mobile-nav a{
    align-self:center;
  }
}