.item-page {
  height: 100vh;
  width: 100vw;
  background-image: url("../routes/imgs/desktop-1.jpg");
  margin: 0px;
  display: flex;
  justify-content: space-evenly;
}

.item-info {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  width: 25%;
}

.item-image img {
  object-fit: contain;
  width: 30vw;
}

.item-add {
  margin-top: 20px;
  background-color: #f8ebe6;
  font-family: "crewniverse";
  height: 5vh;
}

@media screen and (max-width: 460px) {
  .item-page {
    display: block;
  }

  .item-image {
    width: 100%;
  }

  .item-image img {
    width: 80vw;
    margin-left: 10vw;
  }

  .item-info {
    width: 100%;
    text-align: center;
  }
}
