.items {
  text-decoration: none;
  color: black;
}

.items p {
  font-weight: bolder;
}

body {
  background-image: url(../routes/imgs/desktop-1.jpg);
  background-size: cover;
}

.item-field {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

.item-field p {
  font-family: "Roboto", sans-serif;
}
.item {
  border-radius: 5px;
  margin: 30px;
}



.item img {
  object-fit: cover;
  width: 100%;
}

.shop-page {
  width: 100vw;
  min-height: 100vh;
}
.shop-page h1 {
  margin: 0px;
  text-align: center;
  font-family: "snow-white";
}

.item-page {
  margin: 50px;
  display: flex;
  justify-content: space-evenly;
}

.item-info {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.add-button {
  background-color: #f8ebe6;
  font-family: "crewniverse";
  height: 5vh;
  margin: 0px auto;
  cursor: pointer;
}

.item:hover {
  background-blend-mode: overlay;
  box-shadow: 5px 2px 10px rgba(173, 55, 228, 0.849);
}
